import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, BtnExport } from 'components/common/button';
import { Card, Row, Col, Table, message } from 'antd';
import { CartHeadAction } from 'components/common';
import { getPrizeNotes, deletePrizeNotes, exportPrizeNotes } from 'services/addPrizeNote';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import FormFilterScholarship from './FormFilterScholarship';

class ListScholarshipPage extends Component {
  state = {
    loading: false,
    paramSearch: {},
    dataTable: [],
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    const paramSearch = getQueryParams();
    this.handleSearch(paramSearch);
    this.setState({ paramSearch });
  };

  handleSearch = async paramSearch => {
    if (
      !!paramSearch.trainTypeId &&
      !!paramSearch.facultyId &&
      !!paramSearch.courseId &&
      !!paramSearch.studyFieldId &&
      !!paramSearch.yearEduId &&
      !!paramSearch.semesterId
    ) {
      this.setState({ loading: true });
      const result = await getPrizeNotes(paramSearch);
      this.setState({
        dataTable: result.data.items,
        loading: false,
      });
    }
  };

  handleDelete = async id => {
    const { paramSearch } = this.state;
    await deletePrizeNotes(id);
    message.success('Xóa thành công');
    this.handleSearch(paramSearch);
  };

  handleExport = async ({
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    semesterId,
    yearEduId,
    studentCode,
    studentFullName,
  }) => {
    try {
      await exportPrizeNotes({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
        semesterId,
        yearEduId,
        studentCode,
        studentFullName,
      });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Nơi sinh',
        dataIndex: 'pob',
        key: 'pob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số QĐ',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngày QĐ',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
        render: (text, record) => dateToString(record.approvalDate, 'DD/MM/YYYY'),
      },
      {
        title: 'Nội dung',
        dataIndex: 'description',
        key: 'description',
      },
    ];
    const listParamSearch = this.state.paramSearch;
    const disabledExport = !this.state.dataTable.length;
    return (
      <Card title="Nhập khen thưởng">
        <Row>
          <CartHeadAction />
          <Col span={24}>
            <FormFilterScholarship />
          </Col>
        </Row>
        <Row>
          <BtnAddNew href="#/scholarship/addForm" />
          <BtnExport
            disabled={disabledExport}
            title="Xuất Excel"
            onClick={() =>
              this.handleExport({
                trainTypeId: listParamSearch.trainTypeId,
                facultyId: listParamSearch.facultyId,
                courseId: listParamSearch.courseId,
                studyFieldId: listParamSearch.studyFieldId,
                classId: listParamSearch.classId,
                semesterId: listParamSearch.semesterId,
                yearEduId: listParamSearch.yearEduId,
                studentCode: listParamSearch.studentCode,
                studentFullName: listParamSearch.studentFullName,
              })
            }
          />
        </Row>
        <Row>
          <Col span={24}>
            <Table
              loading={this.state.loading}
              bordered
              dataSource={this.state.dataTable}
              columns={columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListScholarshipPage />
  </Page>
);



