import React, { Component } from 'react';
import { Col, Form, Input, Row } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
import './style.scss';

@Form.create({ name: 'formFilterScholarship' })
class FormFilterScholarship extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    udpateQueryParams();
  };

  // handleExport = async ({ trainTypeId, semesterId, yearEduId }) => {
  //   try {
  //     await exportPrizeNotes({
  //       trainTypeId,
  //       semesterId,
  //       yearEduId,
  //     });
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };

  render() {
    const { getFieldDecorator } = this.props.form;
    // const listParamSearch = this.state.paramSearch;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label scholarship-filter" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} span={5} />
          <Col span={5}>
            <Form.Item label="Mã SV">{getFieldDecorator('studentCode', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Họ tên">
              {getFieldDecorator('studentFullName', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>  
        </Row>
      </Form>
    );
  }
}

export default FormFilterScholarship;



